import React, { Component } from 'react';
import { Row, Col, Button, Icon, Table, Form, Popover } from 'antd';
import BarCode from '../Common/BarCode';
import Storage from '../../utils/localStorage'
import BlInput from '../Common/SearchForm/BlInput'
import orderMgtUrl from '../../api/orderMgtUrl'
import utils from '../../utils/utils'
import exportSheet from '../../utils/exportSheet'

class OrderDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paperNo: props.match.params.id || '',
      orderMsg: Storage.get("orderMsg") || {},

      formLayout: 'vertical',
      pageNum: 1,
      pageSize: 10,
      total: 0,
      loading: false,
      exportLoading: false,
      barcode: '', // 条码
      erpGoodsNo: '', // 商品编码
      vendorGoodsNo: '', // 货号

      columns: [
        {
          title: '货号',
          dataIndex: 'vendorGoodsNo',
          width: 60
        },
        {
          title: '条码',
          dataIndex: 'barcode',
          width: 70
        },
        {
          title: '商品编码',
          dataIndex: 'erpGoodsNo',
          width: 90
        },
        {
          title: '商品名称',
          dataIndex: 'goodsName',
          width: 90
        },
        {
          title: '单位',
          dataIndex: 'unit',
          width: 70
        },
        {
          title: '规格',
          dataIndex: 'spec',
          width: 70
        },
        {
          title: '订单金额',
          dataIndex: 'amount',
          width: 90
        },
        {
          title: '订单数量',
          dataIndex: 'qty',
          width: 90
        },
        {
          title: '收货数量',
          dataIndex: 'receiveQty',
          width: 90
        },
        {
          title: '赠品数量',
          dataIndex: 'giftQty',
          width: 90
        },
        {
          title: '收货单价',
          dataIndex: 'price',
          width: 90
        },
        {
          title: '收货总金额',
          dataIndex: 'totalReceiveAmount',
          width: 110
        },
        {
          title: '备注',
          dataIndex: 'remark',
          width: 120
        }
      ],
      tableData: [],
      exportConfig: {
        vendorGoodsNo: '货号',
        barcode: '条码',
        erpGoodsNo: '商品编码',
        goodsName: '商品名称',
        unit: '单位',
        spec: '规格',
        amount: '订单金额',
        qty: '订单数量',
        receiveQty: '收货数量',
        giftQty: '赠品数量',
        price: '收货单价',
        totalReceiveAmount: '收货总金额',
        remark: '备注'
      }
    }
  }

  componentDidMount() {
    this.doSearchHandel()
  }

  render() {
    let {paperNo, storeName, createUserName, createUserContact, authDatetime, expectDatetime} = this.state.orderMsg;
    let tableData = this.state.tableData;
    let columns = this.state.columns;
    return (
      <div className="main-container">
        <div className="operation-wraper">
          <Button type="primary" href={`/886072e393710563/print/${paperNo}`} target="_blank"><Icon type="printer" />打印</Button>
        </div>
        <hr className="s-slip"/>
        <div className="operation-wraper">
          <Row type="flex" align="bottom" gutter={10}>
            <Col span={16}>
              <Row>
                <Col span={12}>
                  门店/仓库：{storeName || '--'}
                </Col>
                <Col span={12}>
                  订单编号：{paperNo}
                </Col>
              </Row>
              <Row>
                <Col span={12}>
                  联系人: {createUserName || '--'}
                </Col>
                <Col span={12}>
                  联系电话: {createUserContact || '--'}
                </Col>
              </Row>
              <Row>
                <Col span={12}>单据时间: {authDatetime || '--'}</Col>
                <Col span={12}>期望送达时间: {expectDatetime || '--'}</Col>
              </Row>
            </Col>
            <Col span={8}>
              <BarCode paperNo={paperNo}/>
            </Col>
          </Row>
        </div>
        <hr className="s-slip"/>
        <div className="search-wraper">
          <Form layout={this.state.formLayout}>
            <BlInput label="货号" name="vendorGoodsNo" defaultVal={this.state.vendorGoodsNo} formItemChange={this.formItemChange}/>
            <BlInput label="条码" name="barcode" defaultVal={this.state.barcode} formItemChange={this.formItemChange}/>
            <BlInput label="商品编码" name="erpGoodsNo" defaultVal={this.state.erpGoodsNo} formItemChange={this.formItemChange}/>
            <Form.Item
              className="btn-wrap"
              label="&nbsp; ">
              <Button type="primary" onClick={this.doSearchHandel.bind(this, true)}><Icon type="search" />查询</Button>
              <Button onClick={this.doResetHandel.bind(this)}><Icon type="reload" />重置</Button>
              <Popover content="每次最多可导出2000条数据" trigger="hover">
                <Button onClick={this.doExportDataHandel.bind(this)} disabled={this.state.exportLoading}>{this.state.exportLoading ? <Icon type="loading" /> : <Icon type="download" />}导出</Button>
              </Popover>
            </Form.Item>
          </Form>
        </div>
        <div className="content-wraper">
          <Table 
            rowKey={item => item.unique}
            columns={columns} 
            dataSource={tableData} 
            loading={this.state.loading}
            pagination={
              {
                size: "middle",
                pageSizeOptions: ['10', '50', '100', '150', '200'],
                showSizeChanger: true,
                showQuickJumper: true,
                defaultCurrent: this.state.pageNum,
                total: this.state.total,
                onShowSizeChange: this.onPageSizeChange,
                onChange: this.pageNumChange,
                showTotal: this.showTotal
              }
            }
            scroll={{ x: 1220 }} />

          <hr className="s-slip"/>
          <div className="footer-wrap">
            <Button type="primary" onClick={this.goBackHandel}><Icon type="left" />返回</Button>
          </div>
        </div>
      </div>
    )
  }

  formItemChange = (result) => {
    this.setState({
      [result.name]: result.value
    })
  }

  onPageSizeChange = (current, pageSize) => {
    this.setState({
      pageSize: pageSize
    }, () => {
      this.doSearchHandel()
    })
  }

  pageNumChange = (pageNumber) => {
    this.setState({
      pageNum: pageNumber
    }, () => {
      this.doSearchHandel()
    })
  }

  showTotal = () => {
    return `共 ${this.state.total} 条`;
  }

  doSearchHandel = (param) => {
    let {pageNum, pageSize, barcode, erpGoodsNo, vendorGoodsNo, paperNo} = this.state
    if(param) {
      pageNum = 1
    }
    this.setState({
      loading: true
    })
    let _params = {pageNum,pageSize,barcode,erpGoodsNo,vendorGoodsNo, paperNo}
    this.$http.post(orderMgtUrl.orderDetail, {..._params}).then(res => {
      this.setState({
        loading: false
      })
      if(res.code === 0) {
         let tableDataList = res.data && res.data.result && res.data.result.length ? res.data.result : []
         let tableData = tableDataList.map(item => {
          item.unique = `${item.barcode}${item.erpGoodsNo}${item.vendorGoodsNo}`
          return item 
         })
        this.setState({
          tableData: tableData,
          total: res.data.total
        })
        return
      }
      throw new Error(res.msg)
    }).catch(e => {
      this.setState({
        loading: false
      })
      utils.openNotification('error', '系统提示', e.message || '')
    })
  }

  doExportDataHandel = () => {
    this.setState({
      exportLoading: true
    })
    let _params = {
      pageNum: 1,
      pageSize: 2000,
      barcode: this.state.barcode,
      erpGoodsNo: this.state.erpGoodsNo,
      vendorGoodsNo: this.state.vendorGoodsNo,
      paperNo: this.state.paperNo
    }
    let paperNo = this.state.paperNo
    this.$http.post(orderMgtUrl.orderDetail, {..._params}).then(res => {
      this.setState({
        exportLoading: false
      })
      if(res.code === 0) {
        let tableDataList = res.data && res.data.result && res.data.result.length ? res.data.result : []
        if(tableDataList.length > 0) {
          exportSheet.exportFileByConfig(tableDataList, `${paperNo}详情.xlsx`, this.state.exportConfig)
        }else {
          utils.openNotification('warning', '系统提示', '没有查询到可导出数据')
        }
        return
      }
      throw new Error(res.msg)
    }).catch(e => {
      this.setState({
        exportLoading: false
      })
      utils.openNotification('error', '系统提示', e.message || '')
    })
  }

  goBackHandel = () => {
    const {history} = this.props
    history.push(`/order_mgt`)
  }

  doResetHandel = () => {
    this.setState({
      pageNum: 1,
      pageSize: 10,
      total: 0,
      barcode: '', // 条码
      erpGoodsNo: '', // 商品编码
      vendorGoodsNo: '', // 货号
      tableData: []
    }, () => {
      this.doSearchHandel(true)
    })
  }

  componentWillUnmount() {
    Storage.remove("orderMsg")
  }
}

export default OrderDetail;